/**
 * Custom VeeValidate validation rule to validate minimum dimensions for an image (instead of exact dimensions, which is
 * how the default validator checks)
 *
 * @param file
 * @param width
 * @param height
 * @returns {Promise<any>}
 */
const validateImage = (file, width, height) => {
    const URL = window.URL || window.webkitURL;
    return new Promise(resolve => {
        const image = new Image();
        image.onerror = () => resolve({ valid: false });
        image.onload = () => resolve({
            valid: image.width >= Number(width) && image.height >= Number(height)
        });

        image.src = URL.createObjectURL(file);
    });
};

const validate = (files, [width, height]) => {
    const list = [];
    for (let i = 0; i < files.length; i++) {
        // if file is not an image, reject.
        if (! /\.(jpg|svg|jpeg|png|bmp|gif)$/i.test(files[i].name)) {
            return false;
        }

        list.push(files[i]);
    }

    return Promise.all(list.map(file => validateImage(file, width, height)));
};

export {
    validate
};

export default {
    validate
};


